import AddModal from "@/components/modal/add";
import "swiper/swiper-bundle.css";
import SeeAll from "../buttons/seeall";
import lottie from "vue-lottie/src/lottie.vue";
import * as animationData from "~/assets/lottie/threePoint.json";

export default {
  components: {
    AddModal,
    SeeAll,
    lottie,
  },
  data() {
    return {
      trendingMovies: [],
      eyeVisibleId: "",
      swiperOptions: {
        slidesPerView: 9,
        spaceBetween: 30,
        breakpoints: {
          320: {
            slidesPerView: 2,
            spaceBetween: 10,
          },
          768: {
            slidesPerView: 3,
            spaceBetween: 30,
          },
          1224: {
            slidesPerView: 4,
            spaceBetween: 20,
          },
        },
      },
      anim: null, // for saving the reference to the animation
      lottieOptions: { animationData: animationData.default },
    };
  },
  methods: {
    onToggleEye(movieId) {
      this.eyeVisibleId = this.eyeVisibleId === movieId ? "" : movieId;
    },
  },
  created() {
    setTimeout(() => {
      this.trendingMovies = this.$store.state.AllOfTrendingMovies;
      console.log(
        "trending movies in carousel component ===>",
        this.trendingMovies
      );
    }, 3000);
  },
};
